/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *:before, *:after {
  box-sizing: content-box; }

:root {
  --color-text: #fff;
  --color-text-light: #c4c4c4;
  --color-body-backgroud: #121212;
  --color-link: #696969;
  --color-link-hover: #f0f0f0;
  --color-colorful: #1cbbb4;
  --font-size-small: 0.95em;
  --logo-url: url(../images/logo.png); }
  .body--light {
    --color-text: #121212;
    --color-text-light: #4b4b4b;
    --color-body-backgroud: #fff;
    --color-link: #212121;
    --color-link-hover: #3c3c3c;
    --logo-url: url(../images/logo-dark.png); }

body {
  background: var(--color-body-backgroud);
  color: var(--color-text);
  font-family: 'Raleway', sans-serif;
  font-size: 1em;
  font-weight: 300;
  line-height: 1.618;
  position: relative; }

a {
  color: var(--color-link);
  text-decoration: none;
  transition: all .15s linear; }
  a:hover {
    color: var(--color-link-hover); }

.container {
  width: 90%;
  min-width: 800px;
  max-width: 1280px;
  margin: 0 auto; }

.flex {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center; }

.flex--vertical {
  display: flex;
  flex-direction: column; }

.list-unstyled {
  list-style: none; }

.header {
  line-height: 40px;
  padding-top: 2em;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; }
  .header__logo h1 a {
    background: var(--logo-url) no-repeat left center;
    background-size: cover;
    display: block;
    width: 220px;
    height: 31px;
    text-indent: -9999em; }
  .header__logo h2 {
    color: var(--color-text-light);
    font-size: var(--font-size-small); }
  .header__nav li a {
    border-bottom: 1px solid transparent;
    display: block; }
  .header__nav li.active a {
    color: var(--color-link-hover);
    border-bottom-color: var(--color-link-hover); }
  .header__nav li:not(:last-child) {
    margin-right: 2em; }
  .header__nav .demo a {
    background: var(--color-colorful);
    color: #fff;
    border-radius: 20px;
    padding: 0 1em; }
    .header__nav .demo a:hover {
      opacity: .75; }

.main {
  position: relative; }
  .main.flex--main {
    width: 100%; }
  .main .gem {
    width: 33%;
    z-index: 2; }
  .main .lines {
    width: 17%;
    width: 67%;
    z-index: 1; }
    .main .lines svg {
      position: relative;
      top: -2px;
      left: -4px; }
  .main .article * {
    transition: all .15s ease-out; }
  .main .article .line {
    fill: #868686; }
  .main .article .circle-big {
    fill: #868686; }
  .main .article .circle-small {
    fill: #121212; }
  .main .article .text,
  .main .article .text-sub {
    fill: #898989; }
    .main .article .text a,
    .main .article .text-sub a {
      fill: inherit; }
  .main .article .text {
    font-size: 15px; }
  .main .article .text-sub {
    font-size: 8px;
    opacity: 0; }
  .main .article .c-g {
    transform-origin: 60% 50%; }
    .main .article .c-g .c-path {
      fill: none;
      stroke: #868686; }
    .main .article .c-g .c-circle {
      fill: #868686;
      stroke: none; }
    .main .article .c-g .c-rect {
      fill: none;
      stroke: none; }
  .main .article:hover .line {
    fill: #1CBBB4; }
  .main .article:hover .circle-big {
    fill: #1CBBB4; }
  .main .article:hover .circle-small {
    fill: #1CBBB4; }
  .main .article:hover .text {
    cursor: pointer;
    fill: #fff; }
  .main .article:hover .text-sub {
    cursor: pointer;
    fill: #a9a9a9;
    opacity: 1; }
  .main .article:hover .c-g {
    transform: rotate(180deg); }
    .main .article:hover .c-g .c-path {
      stroke: #1CBBB4; }
    .main .article:hover .c-g .c-circle {
      fill: #1CBBB4; }
    .main .article:hover .c-g .c-rect {
      fill: #1CBBB4;
      stroke: #1CBBB4; }
  .main .g1 .text {
    transform: matrix(1, 0, 0, 1, 180, 25); }
  .main .g1 .text-sub {
    transform: matrix(1, 0, 0, 1, 180, 42); }
  .main .g1:hover .text {
    transform: matrix(1, 0, 0, 1, 180, 20); }
  .main .g1:hover .text-sub {
    transform: matrix(1, 0, 0, 1, 180, 35); }
  .main .g2 .text {
    transform: matrix(1, 0, 0, 1, 180, 88); }
  .main .g2 .text-sub {
    transform: matrix(1, 0, 0, 1, 180, 105); }
  .main .g2:hover .text {
    transform: matrix(1, 0, 0, 1, 180, 85); }
  .main .g2:hover .text-sub {
    transform: matrix(1, 0, 0, 1, 180, 98); }
  .main .g3 .text {
    transform: matrix(1, 0, 0, 1, 180, 160); }
  .main .g3 .text-sub {
    transform: matrix(1, 0, 0, 1, 180, 176); }
  .main .g3:hover .text {
    transform: matrix(1, 0, 0, 1, 180, 155); }
  .main .g3:hover .text-sub {
    transform: matrix(1, 0, 0, 1, 180, 168); }
  .main .g4 .text {
    transform: matrix(1, 0, 0, 1, 180, 224); }
  .main .g4 .text-sub {
    transform: matrix(1, 0, 0, 1, 180, 238); }
  .main .g4:hover .text {
    transform: matrix(1, 0, 0, 1, 180, 217); }
  .main .g4:hover .text-sub {
    transform: matrix(1, 0, 0, 1, 180, 231); }

.main article.section {
  width: 70%;
  margin: 0 auto; }
  .main article.section .icon {
    margin-bottom: 2rem; }
    .main article.section .icon img {
      display: block;
      width: 75px;
      height: 75px; }
  .main article.section header h3 {
    font-size: 2em;
    margin-bottom: 2rem; }
  .main article.section .entry {
    margin-bottom: 2em; }
    .main article.section .entry p:not(:last-child) {
      margin-bottom: 1em; }
  .main article.section .video {
    position: relative; }
    .main article.section .video a {
      background: rgba(0, 0, 0, 0.1);
      display: block;
      position: relative;
      width: 195px; }
    .main article.section .video img {
      display: block;
      width: 100%; }
    .main article.section .video .btn-play {
      background: rgba(0, 0, 0, 0.5);
      border-radius: 50%;
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 38px;
      height: 38px;
      transform: translate(-50%, -50%); }

.footer {
  font-size: var(--font-size-small);
  line-height: 4em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }
  .footer a {
    color: var(--color-link); }
  .footer__designer span {
    color: red;
    font-weight: bold; }
  .footer__nav li:not(:last-child) {
    margin-right: 2em; }

.loading-indicator {
  transform-origin: center center;
  z-index: 5; }

.inside-loading {
  background: rgba(30, 100, 10, 0.8);
  background: #fff;
  border-radius: 50%;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-origin: center center;
  opacity: 0;
  visibility: hidden;
  z-index: 5;
  transition: visibility .1s linear, opacity .15s linear, transform .35s ease-in; }
  .is-showing-inside-loading .inside-loading {
    transform: scale(50);
    opacity: 1;
    visibility: visible; }

